import * as v from '../../styles/variables'

const styles = {
  code: {
    backgroundColor: v.white.darken(0.05).string(),
    color: v.black.lighten(5).string(),
    fontFamily: '"Lucida Console", Monaco, monospace',
    padding: `${v.ms1}rem`
  }
}

export default styles



