import * as v from '../../styles/variables'

const styles = {
  main: {
    minHeight: `${10 * v.lnht}rem`,
    padding: {
      top: `${2 * v.lnht}rem`,
      right: 0,
      bottom: `${2 * v.lnht}rem`,
      left: 0
    }
  }
}

export default styles



